.Main {
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden;
  position: relative;
  z-index: 2;
}

.Main .Slider {
  position: absolute;
  height: 200px;
  width: 120px;
  top: 10%;
  left: 45%;
  transform-style: preserve-3d;
  transform: perspective(1000px);
  animation: autoRun 60s linear infinite;


}

@keyframes autoRun{
  from{
    transform: perspective(1000px) rotateX(-12deg) rotateY(0deg);
    }to{
   transform: perspective(1000px) rotateX(-12deg) rotateY(360deg);
  }
} 

.Main .Slider .items {
  position: absolute;
  inset: 0;
  transform: rotateY(calc((var(--position) - 1) * (360 / var(--quantity)) * 1deg)) translateZ(550px);

}

.Main .Slider .items img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}



.text-with-border {
  font-size: 120px;
  font-weight: bold;
  color: white;
  position: relative;
  mix-blend-mode: difference;
  z-index: 3; /* Makes the text transparent when something is behind it */
}

.text-with-border::after {
  content: attr(data-text); /* Same text content */
  position: absolute;
  top: 0;
  left: 0;
  color: transparent; /* Text fill is transparent */
 /* Gray border around the text */
  padding: 2px;
  mix-blend-mode: normal; /* Keep the border normal */
}

.box {
  height: 200px;
  width: 200px;
  border: 2px solid transparent;
  border-radius: 10px;
  position: relative;
  animation: borderAnimation 2s infinite;
}

.box::before {
  content: '';
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  border-radius: 10px;
  border: 2px solid white;
  opacity: 0;
  animation: borderPulse 2s infinite;
}

@keyframes borderAnimation {
  0% {
    border-color: transparent;
  }
  50% {
    border-color: transparent;
  }
   100% {
   border-color: white;
  }
}

@keyframes borderPulse {
  0% {
    opacity: 0;
    transform: scale(1);
    }
    50% {
      opacity: 1;
      transform: scale(1.1);
    }
    100% {
    opacity: 0;
    transform: scale(1);
    }
}